.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #282c34;
}

.calculator {
  width: 300px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.display {
  background-color: #222;
  color: white;
  text-align: right;
  padding: 20px;
  font-size: 24px;
}

.input {
  font-size: 18px;
}

.result {
  font-size: 32px;
  margin-top: 10px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 20px;
}

button {
  padding: 20px;
  font-size: 18px;
  background-color: #f1f1f1;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:active {
  background-color: #ddd;
}

.zero {
  grid-column: span 2;
}
